html{
    overflow: hidden;
}
.Alternative{
    background-color: #f5f5f9;
    position: relative;
    top: 50px;
}
p{
    margin: 0;
    padding: 0;
}
.ulList{
    position: static;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    overflow: hidden;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.ulList li{
    height: 274px;
    background-color: white;
    float: left;
    position: relative;
    margin-top: 5px;
}
.ulList li:last-child{
    margin-bottom: 20px;
}
.ulList >li >.describeImg{
    height: 170px;
    /* width: 100%; */
    border-bottom: 1px solid #f5f5f9;
    box-sizing: border-box;
    position: relative;
    padding: 0 8px;
    
}
.ulList>li>.describeImg>img{
    width: 160px;
    height: 160px;
    position: absolute;
    left: 50%;
    margin-left: -80px;
    top: 50%;
    margin-top: -80px;
}
.ulList >li >.describeText{
    text-align: center;
    width: 100%;
    height: 100px;
}
.ulList >li >.describeText  >img{

    -webkit-touch-callout:none;  /*系统默认菜单被禁用*/  
    -webkit-user-select:none; /*webkit浏览器*/  
    -khtml-user-select:none; /*早期浏览器*/  
    -moz-user-select:none;/*火狐*/  
    -ms-user-select:none; /*IE10*/  
    user-select:none; 
}

.unavailable{
    position: absolute;
    left: 0;
    top: 0;
    /* padding-left: 15px;
    padding-right: 15px; */
    padding: 0 8px;
}
.remix{
justify-content: center;
align-items: center
}
.remix>.remix-left{
  position: absolute;
  left: 12px;  
}
