/*
 * 弹出层
 */
 .popups {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	-webkit-align-items: center;
	justify-content: center;
	align-items: center;
	z-index: 10000;
	background-color: rgba(0,0,0,0.3)
}
.popups > .mengban{
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.3);
}
.popups >.loaidngWrap {
	width: 120px;
	height: 120px;
	background-color: #FFF;
	/* border-radius: 0.8rem; */
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 50%;
    margin-top: -60px;
	left: 50%;
	margin-left: -60px;
}
.LoadingImg{
	width: 50px;
	height: 10px;
	margin: 39px 35px 32px 35px;
}
.loadingContent{
	width: auto;
	height: 22px;
	font-family: HelveticaNeue;
	font-size: 15px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.47;
	letter-spacing: normal;
	text-align: center;
	color: #1b1b1b;
}